import { Component, Vue } from 'vue-property-decorator'
import { diceCup, Die } from '@/interfaces/dice'

@Component
export default class WHDiceRoller extends Vue {
  diceCup: diceCup = { dice: [], hasRolled: false };
  numberList: number[] = [1, 2, 3, 4, 5, 6];
  selectedValues: number[] = [];

  dieClass (value: number): any {
    return {
      '--one': value === 1,
      '--two': value === 2,
      '--three': value === 3,
      '--four': value === 4,
      '--five': value === 5,
      '--six': value === 6,
      '--selected': this.selectedValues.some(sel => sel === value)
    };
  }

  addDie () {
    const newDie: Die = { value: Math.floor(Math.random() * 6) + 1 };
    this.diceCup.dice.push(newDie);
    this.diceCup.hasRolled = false;
  }

  removeDie () {
    this.diceCup.dice.pop();
    this.selectedValues = [];
    this.diceCup.hasRolled = false;
  }

  clearDice () {
    this.selectedValues = [];
    this.diceCup.dice = [];
    this.diceCup.hasRolled = false;
  }

  setNumDice () {
    let prompt = window.prompt('Please enter the number of dice you want to roll');
    let numDice = parseInt(prompt!, 10);
    if (numDice > 0 && numDice <= 100000) {
      this.clearDice();
      for (let i = 0; i < numDice; i++) {
        this.addDie();
      }
    } else {
      alert('Number must be between 1 and 100000');
    }
    
    this.diceCup.hasRolled = false
  }

  rollDice () {
    if(this.selectedValues.length === 0) {
      this.diceCup.dice.forEach(die => {
        die.value = Math.floor(Math.random() * 6) + 1
      });
    } else {
      this.diceCup.dice.forEach(die => {
        if (this.selectedValues.some(sel => sel === die.value)) {
          die.value = Math.floor(Math.random() * 6) + 1
        }
      });
    }
    this.selectedValues = [];
    this.diceCup.hasRolled = true;
  }

  removeAllBelowSelected () {
    if(this.selectedValues.length === 1){
      const value = this.selectedValues[0];
      this.diceCup.dice = this.diceCup.dice.filter(die => die.value >= value);
    }
    this.selectedValues = [];
  }

  updateSelected (value:number) {
    if (this.selectedValues.some(sel => sel === value)) {
      this.selectedValues = this.selectedValues.filter(sel => sel !== value);
    } else {
      this.selectedValues.push(value);
    }
  }

  get totalDice () {
    if (this.selectedValues.length === 0) {
      return this.diceCup.dice.length;
    } else {
      let total = 0;
      for (let i=0; i+1 <= this.selectedValues.length; i++) {
        total += this.totalDiceAtValue(this.selectedValues[i]);
      }
      return total;
    }
  }

  totalDiceAtValue (value:number) {
    if(this.diceCup.hasRolled){
      return this.diceCup.dice.filter(die => die.value === value).length
    } else {
      return 0;
    }
  }
}
